const LogPref = ''
const SmallNum = 0.000001
const nozeroValidator = (rule, value, callback) => {
  if (+value === 0) {
    return callback(new Error('0'))
  } else {
    callback()
  }
}

export const getRequierdSelect = option => {
  return { required: true, message: `${option.label}` }
}
export const getRequierdInput = option => {
  return { required: true, message: `${option.label}` }
}
export const getRangeNumber = option => {
  const { min, max, larger, less } = option
  if (
    min === undefined &&
    max === undefined &&
    larger === undefined &&
    less === undefined
  ) {
    console.log(LogPref + '')
    return {}
  }

  if (min !== undefined && max !== undefined) {
    return {
      type: 'number',
      min,
      max,
      message: `${min}~${max}`
    }
  } else if (max !== undefined) {
    return { type: 'number', max, message: `${max}` }
  } else if (min !== undefined) {
    return { type: 'number', min, message: `${min}` }
  }

  if (larger !== undefined && less !== undefined) {
    return {
      type: 'number',
      min: larger + SmallNum,
      max: less - SmallNum,
      message: `${larger}${less}`
    }
  } else if (larger !== undefined) {
    return {
      type: 'number',
      min: larger + SmallNum,
      message: `${larger}`
    }
  } else if (less !== undefined) {
    return {
      type: 'number',
      max: less - SmallNum,
      message: `${less}`
    }
  }
}

export default {
  // 
  mobile: {
    len: 11,
    message: '',
    trigger: 'blur'
  },
  // 
  emial: { type: 'email', message: '', trigger: 'blur' },
  // 
  number: { type: 'number', message: '', trigger: 'blur' },
  // 
  nozero: { type: 'message', trigger: 'blur', validator: nozeroValidator }
}
